@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700,900);
@import url(http://fonts.googleapis.com/css?family=Roboto:400,700,900);
.app-container {
  display: flex;
  height: 100%;
  width: 100%;
}

.page-block {
  width: 90%;
  max-width: 1040px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.page-container {
  background-color: #2C2D2F;
  height: 100%;
  overflow-y: scroll;
}

.header {
  background-color: #17181C;
  width: 228px;
  top: 0px;
  height: 100%;
  display: flex;
  color: white; 
  flex-direction: column;
  z-index: 999;
  border-right: 1px solid #202226;
  overflow-y: scroll;
}

.content {
  width: 100%;
}

.profile-pic-container {
  width: 100%;
  height: 200px;
  position: relative;
}

.profile-pic-container img {
  max-height: 100%;
  max-width: 100%;
  height: auto;
  width: 100%;
}



body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  line-height: 1.65;
  color: #e1e1e1;
  background-color: #6B6C6D;
}

.header-name {
  height: 44px;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  line-height: 44px;
  white-space: nowrap;
  display: inline-block;
  font-size: 20px;
  background-color: #00A3E1;
  opacity: 0.8;
  font-weight: 700;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}

.sidebar-items {
  height: 70%;
}

.h-100 {
  height:
}


.footer {
  height: 5%;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h2, h3 {
  font-family: "Playfair Display", serif;
}

.hidden {
  display: none;
}


.section-subheading h3 {
  border-bottom: 2px solid #00A3E1;
  text-transform: uppercase;
  font-size: 22px;
  display: inline-block;
  font-weight: 600;
  line-height: 1em;
  letter-spacing: .5px;
  padding: 5px 10px;
}

.section-subheading h3:after, .section-subheading h3:before{
  position: absolute;
  content: "";
  width: 2px;
  height: 5px;
  bottom: 0;
}


.section-subheading {
  width: 100%;
  margin-bottom: 50px;
  text-align: center;
}



.mb-20 {
    margin-bottom: 20px;
}

.mt-30 {
    margin-top: 30px;
}

p {
    line-height: 1.65;
}

ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
}
.navbar-container ul {
    padding: 0px;
    margin-top: 0;
    margin-bottom: 1rem;
}
.navbar-container li {
    font-size: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
    border-bottom: 1px solid #202226;
    line-height: 45px;
    cursor: pointer;
}

.menu-item {
    display: inline-block;
    color: #777;
    cursor: pointer;
    background-color: transparent;
}

.menu-label {
    position: relative;
    left: 51px;
    top: 5px;
}

.navbar-container .lnr {
    position: relative;
    left: 42px;
    top: 7px;
    font-size: 20px;
}

.lnr {
    font-family: 'Linearicons-Free';
    font-style: normal;
    font-weight: normal;
    font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
}

.footer-container {
    height: 100%;

}

.footer-items {
    height: inherit;
    display: flex;
    justify-content: space-evenly;
}

.footer-container .fa {
    font-size: 23px;
    color: #777;
}

.home-content-container img {
    height: 100%;
    width: 100%
}

.home-content-container {
    height: 100%;
}

.content {
    height: 100%;
}
.about-info-container, .about-service-container {
    display: flex;
}

.about-service-container {
    flex-direction: column;
}

.about-details {
    width: 66%;
    padding: 0 10px;
}

.service-item {
    text-align: center;
    margin-bottom: 40px;
    padding-right: 15px;
    padding-left: 15px;
}

.service-item .icon {
    font-size: 50px;
    margin-bottom: 10px;
    line-height: 1em;
    color: #00A3E1;
}

.service-item h4 {
    font-size: 18px;
    font-weight: 600;
}

.service-item p {
    font-size: 14.5px;
    line-height: 22px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.about-personal-details {
    width: 34%;
    padding-left: 30px;
    padding-right: 15px;
}


.about-info-container h3 {
    font-size: 22px;
    font-weight: 700;
}

.about-personal-details ul li .title::after {
    position: absolute;
    right: -10px;
    content: ":";
}

.about-personal-details ul li {
    line-height: 1em;
    margin-bottom: 15px;
}
.signature {
    width: 50%;
}
.signature img {
    width: 100%;
    height: auto;
}
.btn-main {
    border: 2px solid #00A3E1;
    padding: 10px 20px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    text-transform: uppercase;
    background-color: transparent;
    transition: all .3s ease;
    cursor: pointer;
    font-size: 14px;
    color: #00A3E1;
    font-weight: 500;
    position: relative;
    line-height: 100%;
    letter-spacing: 1px;
    overflow: hidden;
}

.btn-main:hover {
    color: #000;
    background-color: #00A3E1;
}

.about-personal-details ul li .title {
    font-family: "Playfair Display", serif;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: .5px;
    border-bottom: 2px solid #fff;
    position: relative;
    display: inline-block;
    margin-right: 15px;
    padding-bottom: 1px;
    color: #fff;
    border-color: #00A3E1;
}

.service-list {
    display: flex;
    justify-content: space-between;
}



.page-heading {
    margin-top: 50px;
    margin-bottom: 50px;
    padding-bottom: 10px;
    border-bottom: 1px dashed rgba(255, 255, 255, 0.2);
}

.page-heading .icon {
    float: right;
    font-size: 38px;
    line-height: 1em;
    color: #00A3E1;
  }
  
  .page-heading h2 {
    font-size: 36px;
    font-weight: 700;
    letter-spacing: 1.5px;
    line-height: 1em;
    text-transform: capitalize;
    color: #fff;
    position: relative;
    display: inline-block;
    color: #00A3E1;
    font-family: "Playfair Display", serif;
  } 
.history-container, .skills-container {
    display: flex;
}

.history-container .experience-container, .history-container .education-container {
    width: 50%;
}

.skills-container .programming-container, .skills-container .coding-container {
    width: 50%;
}



.history-item {
    margin: 20px 0 50px;
    list-style: none;
    position: relative;
    padding: 0 0 0 50px;
    width: 100%;
}

.history-item li {
    position: relative;
    margin-left: 10px;
    padding: 20px;
    transition: all .5s ease-in-out;
}

.history-item li .content {
    margin-left: 10px;
}

.history-item li:not(:first-child) {
    margin-top: 60px;
}

.history-item li .content h4 {
    font-size: 18px;
    font-weight: 700;
    letter-spacing: .5px;
    line-height: 26px;
    margin: 0;
    text-transform: uppercase;
}

.history-item li .content h5 {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 12px;
    font-family: "Roboto", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    color: rgb(47, 198, 255);
}

.history-item li .content p {
    font-size: 16px;
    margin-bottom: 0px;
}

.history-item li .year {
    height: 100%;
    left: -10px;
    top: 0;
    position: absolute;
}

.history-item li .year span {
    font-size: 16px;
    position: absolute;
    transform: translateX(-120%);
    color: rgb(232, 230, 227);
}

.history-item li .year .to {
    top: -18px;
}

.history-item li .year .from {
    bottom: -18px;
}

.history-item .line-left {
    width: 2px;
    background-color: rgb(0, 130, 180);
    height: 100%;
    position: absolute;
    top: 0;
    left: -10px;

}

.history-item .line-left::after, .history-item .line-left::before {
    content: "";
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 2px solid rgb(0, 136, 188);
    position: absolute;
    left: -4px;
    top: -10px;
}

.history-item .line-left::after {
    top: 100%
}

